<template>
    <div>
        <Header @push="Pagepush"></Header>
        <div class="main">
            <div class="crumbs-nav">
                <crumbs-nav>
                    <template #home>
                        <div style="margin-top: 5px;" class="home">
                            首頁
                        </div>
                    </template>
                    <template #current>
                        <div style="margin-top: 5px;" class="current">
                            賬號展示
                        </div>
                    </template>
                </crumbs-nav>
            </div>
            <div style="display: flex;width: 100%;justify-content: center;">
                <div class="price-title">{{ $route.query.city }}（{{ $route.query.title }}）</div>
                <div class="numbers">部分賬號展示</div>
            </div>
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="item in 4">
                        <div style="display: flex;justify-content: center;flex-flow: wrap;width: 100%;">
                            <div class="swiper-slide-item">
                                <div class="show-box-button">點擊查看</div>
                            </div>
                            <div class="swiper-slide-item">
                                <div class="show-box-button">點擊查看</div>
                            </div>
                            <div class="swiper-slide-item">
                                <div class="show-box-button">點擊查看</div>
                            </div>
                            <div class="swiper-slide-item">
                                <div class="show-box-button">點擊查看</div>
                            </div>
                            <div class="swiper-slide-item">
                                <div class="show-box-button">點擊查看</div>
                            </div>
                            <div class="swiper-slide-item">
                                <div class="show-box-button">點擊查看</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <GoTop></GoTop>
    </div>
</template>

<script>
import 'swiper/dist/js/swiper'
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'

export default {
    name: "account",
    components: {

    },
    data() {
        return {

        }
    },
    methods: {
        Pagepush() {
            this.$router.push('accountNumberShow_en')
        },
    },
    mounted() {
        this.$router.afterEach((to, from, next) => {
            window, scrollTo(0, 0)
        })
        new Swiper('.swiper-container', {
            // autoplay: {
            //    delay: 3000,
            // },
            direction: 'horizontal',
            loop: true,
            pagination: {
                el: '.swiper-pagination',
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            coverflowEffect: {
                rotate: 0,
                stretch: 0,
                depth: 100,
                modifier: 0.8,
                slideShadows: true,
                scale: 0.5
            },
        })
    },
}
</script>

<style scoped>
.show-box-button {
    width: 134px;
    height: 32px;
    background: url('../assets/img/Button.png') no-repeat;
    background-size: 164px 32px;
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.5);
    border-radius: 38px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 500;
    font-size: 14px;
    color: #0B6FF9;
    line-height: 32px;
    text-align: center;
    font-style: normal;
    cursor: pointer;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 26px;
}

.swiper-slide-item {
    margin-left: 9px;
    width: 168px;
    height: 209px;
    background-image: url('../assets/img/product2.png');
    background-repeat: no-repeat;
    background-size: 168px 209px;
    margin-top: 14px;
    position: relative;
    box-shadow: 1px 1px 1px 1px #EEEEEE;
    border-radius: 8px;
}

.main {
    width: 100%;
    background-image: url('../assets/img/bgt.png');
    background-repeat: no-repeat;
    background-size: 100%;
    overflow: hidden;
    padding-bottom: 20px;
}

.price-title {
    height: 22px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 16px;
    color: #333333;
    line-height: 22px;
    font-style: normal;
    margin-left: 15px;
    margin-top: 15px;
}

.numbers {
    margin-left: 15px;
    margin-top: 15px;
    text-align: right;
    font-family: PingFangSC, PingFang SC;
    font-weight: 500;
    font-size: 14px;
    color: #0B6FF9;
    line-height: 20px;
    text-align: center;
    font-style: normal;
}
</style>


